.hero {
  min-height: 1.5rem;
  min-width: 9rem;
  margin-right: 1rem;
  z-index: 1;
}
.up {
  height: 1.7rem;
  box-sizing: border-box;
  border-radius: 2rem;
  margin-top: -1px;
  background-color: #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.highlight {
  border: 2px solid blue;
}

.icon {
  height: 0.4rem;
  margin-left: 0.5rem;
  object-fit: contain;
  cursor: pointer;
}
.down {
  background-color: #a22323;
  width: 92%;
  margin: 1% 5%;
  border-radius: 0.3rem;
  display: flex;
  flex-direction: column;
}
.item {
  margin: 0.2rem 0.4rem;
}
.itemfocus {
  background-color: lightblue;
}
