* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.hero {
  min-width: 60%;
  height: 100%;
  margin: 0 0.5rem 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img {
  width: 95%;
  height: 70%;
  object-fit: contain;
  border: solid #2e0052;
  border-radius: 0.4rem;
}
.para1 {
  width: 95%;
  padding: 0.2rem 0;
  font-weight: 600;
  font-size: 1rem;
}
.para2 {
  width: 95%;
  padding: 0.2rem 0;
  color: #797979;
  font-size: 0.8rem;
}
