* {
  box-sizing: border-box;
}
.main {
  height: 25vh;
  width: 100%;
  display: flex;
}
.cube1 {
  width: 20%;
  height: 96%;
  display: flex;
  justify-content: center;
  margin: 0.2rem 0.4rem;
  padding: 0.5%;
  border: solid #2e0052;
  border-radius: 0.4rem;
}
.image1 {
  /* height: 100%; */
  width: 100%;
  object-fit: contain;
}
.cube2,
.cube3,
.cube4,
.cube5 {
  width: 15%;
  height: 95%;
  margin: 0.2rem 0.4rem;
  display: flex;
  flex-direction: column;
}
.cube2 {
  width: 25%;
}
.text {
  padding: 0.5rem;
}
.text1 {
  font-weight: 600;
}
.text2 {
  color: #a2a2a2;
}
.choice {
  margin: 0.5rem;
  width: 80%;
}
