@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Inter&family=Roboto:wght@400;500&family=Single+Day&display=swap");
.hero {
  height: 2.8rem;
  width: 100%;
  background-color: #2e0052;
  color: white;
  display: flex;
  justify-content: space-between;
}
.left,
.center,
.plus {
  display: flex;
  justify-content: center;
  align-items: center;
}
.minus {
  width: 10rem;
  font-family: "roboto", sans-serif;
}
.center {
  font-family: "roboto", sans-serif;
  font-size: medium;
  width: 20rem;
}
.plus {
  font-family: "roboto", sans-serif;
  width: 10rem;
}
.num1 {
  height: 50%;
  object-fit: contain;
  margin-right: 0.5rem;
}
.mark {
  margin: 0 0.5rem;
}
.route {
  cursor: pointer;
}

.mcube1 {
  height: 3rem;
  background-color: #2e0052;
}
