* {
  box-sizing: border-box;
}
.hero {
  min-height: 100vh;
  width: 100vw;
}
.img {
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  object-fit: contain;
  border: solid #2e0052;
  border-radius: 0.4rem;
}
.para {
  width: 96%;
  margin: 0;
  padding: 0.3rem 1rem;
  display: block;
  font-size: 1rem;
  line-height: 1.2rem;
}
.btn {
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 1rem;
  cursor: pointer;
}

.para1 {
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 500;
}
.para2 {
  font-size: 1.3rem;
}
.para3 {
  font-weight: 600;
}

.header {
  height: 1.5rem;
  margin-bottom: 0.5rem;
}
.section1 {
  /* Titlebar */
  height: 3rem;
  margin-left: 0.7rem;
  margin-bottom: 0.5rem;
}
.btn1 {
  height: 2rem;
  width: 10rem;
  color: white;
  background-color: #2e0052;
}
.cube1 {
  width: 100%;
  min-height: 10%;
  font-size: 1.5rem;
  padding: 0.2rem 1rem;
  font-weight: 550;

  margin-bottom: 0.5rem;
}
.cube2 {
  width: 100%;
  min-height: 80vh;
  display: flex;
}
.cube21 {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cube211 {
  width: 100%;
  height: 70%;
  padding: 0.5rem;
}
ul {
  width: 90%;
  margin-left: 6%;
}
.img2 {
  height: 80%;
  min-width: 30%;
  margin-right: 0.5rem;
}
.cube212 {
  width: 90%;
  height: 30%;
  margin-left: 0.5rem;
  padding: 0 1rem;
  display: flex;
  overflow: scroll;
}
.cube212::-webkit-scrollbar {
  display: none;
}
.btn2,
.btn3 {
  height: 2rem;
  width: 60%;
  color: black;
  margin: 0.5rem 0 0.5rem 1rem;
  background-color: #ffd600;
}
.btn3 {
  background-color: #ffb800;
}

.cube22 {
  width: 50%;
  /* padding: .5rem; */
}
@media screen and (max-width: 599px) {
  .hero {
    margin-bottom: 3rem;
  }
  .btn1 {
    background-color: #fff;
    height: 2rem;
    width: 2rem;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.33);
    border-radius: 50%;
  }
  .behind {
    height: 80%;
    padding: 0.4rem;
    object-fit: contain;
  }
  .cube2 {
    flex-direction: column;
  }
  .cube21 {
    width: 100vw;
    height: 70vh;
  }
  .cube211 {
    display: none;
  }
  .cube212 {
    width: 100%;
    height: 100%;
  }
  .img2 {
    min-width: 90%;
    height: 90%;
  }
  .cube22 {
    width: 100%;
  }
  .btn2,
  .btn3 {
    margin: 0.5rem auto;
  }
}
