@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Inter&family=Roboto:wght@400;500&family=Single+Day&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.ft {
  height: 2rem;
  width: 100%;
  color: white;
  background-color: #2e0052;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mft {
  height: 3rem;
  width: 100%;
  padding: 0 0.4rem;
  background-color: #c8c8c8;

  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cube {
  height: 2.2rem;
  width: 20%;
  /* background-color: green; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.img {
  height: 70%;
  object-fit: contain;
}
.para1 {
  height: 20%;
  font-weight: 520;
}
