@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Inter&family=Roboto:wght@400;500&family=Single+Day&display=swap");
.head {
  height: 8rem;
  width: 90%;
  border-radius: 1rem;
  background: linear-gradient(90deg, #7286b4 0%, #e794ce 100%);
  display: flex;
  margin: 1rem 5%;
}

.minus {
  width: 60%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.up {
  height: 60%;
  width: 80%;
  display: flex;
  align-items: center;
}
.down {
  height: 40%;
  width: 80%;
  display: flex;
  align-items: center;
}

.para1 {
  color: #2e0052;
  font-family: Roboto;
  font-size: 2rem;
  font-weight: 540;
  letter-spacing: 0em;
}

.t_btn {
  padding: 0.5rem 1.5rem;
  border-radius: 1.5rem;
  color: white;
  font-family: "Roboto";
  background-color: #2e0052;
}

.plus {
  width: 40%;
  height: 100%;
  margin-top: -2rem;
}

.icon {
  height: 10rem;
  width: 100%;

  object-fit: contain;
}
@media screen and (min-width: 400px) and (max-width: 599px) {
  .head {
    margin-top: 2rem;
  }
  .para1 {
    font-size: 1.4rem;
  }
}
@media screen and (max-width: 399px) {
  .head {
    margin-top: 2rem;
  }
  .para1 {
    font-size: 1rem;
  }
}
