@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Inter&family=Roboto:wght@400;500&family=Single+Day&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.hero {
  height: 10vh;
  width: calc(100% - 2rem);
  background-color: #fff;
  display: flex;
  align-items: center;
  margin: 0.5rem;
}

.trademark {
  margin-left: 0.5rem;
  height: 90%;
  object-fit: contain;
}

.nam {
  font-family: "Times New Roman", Times, serif, sans-serif;
  font-size: 24px;
  font-weight: 600;
  margin-left: 10px;
  color: #2e0052;
  letter-spacing: 0em;
}
.hm {
  margin-left: 10px;
  font-family: "Roboto";
  margin-top: 10px;
}
.breadcrum {
  margin-top: 0.8rem;
  margin-left: 0.5rem;
}
.btn1 {
  margin-left: 70%;
  height: 60%;
  padding: 0 1rem;
  background: #1d7000;
  border-radius: 1rem;
  color: white;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.image1 {
  height: 60%;
  margin-right: 0.2rem;
  object-fit: contain;
}
