* {
  box-sizing: border-box;
}
.hero {
  width: 85%;
  min-height: 3rem;
  margin: 0.5rem 7%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0;
}
.up {
  width: 100%;
  height: 3rem;
  border: solid #c8c8c8;
  border-radius: 2rem;
  box-sizing: border-box;
  padding: 0.5rem;
  display: flex;
}
.cube1 {
  width: 5%;
  height: 2rem;
}
.img {
  height: 2rem;
  object-fit: contain;
}
.cube2 {
  width: 95%;
  height: 2rem;
}
.data1 {
  box-sizing: border-box;
  padding: 0.2rem;
  font-size: 1.3rem;
  margin: 0;
  width: 100%;
  height: 100%;
  resize: none;
  border: none;
  outline: none;
  overflow: hidden;
}
.text1 {
  padding: 0.3rem;
  cursor: pointer;
}
.data1::-webkit-scrollbar {
  display: none;
}
.down {
  margin-top: 0.2rem;
  border: solid #c8c8c8;
  border-radius: 0.5rem;
  width: 95%;
  min-height: 1.5rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.5rem;
  background-color: #fff;
}

@media screen and (max-width: 599px) {
  .hero {
    /* background-color: red; */
  }
  .up {
    margin: 0.3rem;
    margin-bottom: 0.2rem;
    height: 2rem;
    border-radius: 0.2rem;
    padding: 0;
    background-color: #fff;
  }
  .img {
    margin: 10% 0 0 3%;
    height: 80%;
  }
  .data1 {
    font-size: 1rem;
    padding: 0.3rem;
  }
  .cube1 {
    height: 100%;
  }
  .cube2 {
    height: 100%;
  }
  .down {
    margin-top: 0;
    z-index: 1;
  }
}
