.hero {
  min-height: 100vh;
}
.cube1 {
  width: 100%;
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cube2 {
  width: 40%;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 23px 2px rgba(0, 0, 0, 0.11);
}
.img1 {
  height: 25%;
  object-fit: contain;
  margin: 0.5rem;
}
.para1 {
  height: 2rem;
  margin: 0.5rem;
  font-weight: 600;
}
.para2 {
  height: 2rem;
  margin: 0.5rem;
  color: #969696;
}
.para3 {
  height: 2rem;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
  border-radius: 0.4rem;
  color: white;
  background-color: #2e0052;
}
@media screen and (max-width: 599px) {
  .cube2 {
    width: 80%;
  }
}
