.hero {
  min-height: 15rem;
  background-color: #f0f0f0;
  box-shadow: 0 0 2px black;
  margin: 1rem 0 0 1rem;
  display: flex;
  flex-direction: column;
}

.deep {
  width: 80%;
  height: 24rem;
  padding: 0 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.cube1 {
  width: 100%;
  height: 70%;
  box-sizing: border-box;
  padding: 0.5rem;
  background-color: #d4e5ff;
  position: relative;
}
.img {
  height: 90%;
  width: 100%;
  object-fit: contain;
}
.img1 {
  background-color: whitesmoke;
  height: 15%;
  left: 72%;
  top: 72%;
  padding: 2%;
  border-radius: 50%;
  object-fit: contain;
  position: absolute;
  cursor: pointer;
}
.cube2 {
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
}
.deepcube2 {
  box-sizing: border-box;
  padding: 3.2rem 1rem;
  height: 100%;
  font-size: 1.5rem;
  line-height: 2rem;
}
span {
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 599px) {
  .hero {
    width: 12rem;
    /* height: 10rem; */
  }
}
