@import url("https://fonts.googleapis.com/css2?family=DM+Sans&family=Inter&family=Roboto:wght@400;500&family=Single+Day&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.hero {
  /* background-color: red; */
  min-height: 100vh;
}
.button {
  height: 100%;
  width: 100%;
  border-radius: 0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.para {
  font-size: 1rem;
}
.cube1 {
  width: 8rem;
  height: 6vh;
  margin-left: 0.5rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  color: white;
  background-color: #2e0052;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.button1 {
  height: 1.5rem;
  width: 10%;
  color: white;
  background-color: #2e0052;
}
.cube2 {
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.para1 {
  font-size: 1.3rem;
  font-weight: 540;
}
.cube3 {
  min-height: 83vh;
  width: 100%;
  display: flex;
}
.cube31 {
  width: 60%;
  padding: 0.5rem;
}
.cube311 {
  /* Address */
  width: 100%;
  height: 20vh;
  padding: 0.5rem;
  border-bottom: solid #e1e1e1;
  display: flex;
}
.cube3111 {
  width: 50%;
}
.para2 {
  display: block;
  padding: 0.2rem;
  color: #b52b00;
  font-family: "Roboto";
  font-weight: 550;
}
.cube3112 {
  width: 50%;
}
.para3 {
  color: #797979;
  font-family: "Roboto";
  display: block;
  padding: 0.2rem;
}
.cube312 {
  /* Payment */
  width: 100%;
  height: 8vh;
  padding: 0.5rem;
  border-bottom: solid #e1e1e1;
  display: flex;
}
.cube3121 {
  width: 50%;
}
.cube3122 {
  width: 50%;
}
.cube313 {
  /* Review */
  width: 100%;
  height: 50vh;
  padding: 0.5rem;
  border-bottom: solid #e1e1e1;
  display: flex;
}
.cube3131 {
  width: 50%;
}
.cube3132 {
  width: 50%;
}
.cube31321 {
  width: 100%;
  height: 80%;
  padding: 0.4rem;
  display: flex;
  overflow: scroll;
}
.cube31321::-webkit-scrollbar {
  display: none;
}
.para4 {
  display: block;
  font-weight: 400;
  padding: 0.1rem 0.2rem;
}
.cube32 {
  /* order total  */
  width: 40%;
  display: flex;
  justify-content: center;
}
.cube321 {
  margin-top: 1rem;
  width: 70%;
  height: 60%;
  border: solid #e1e1e1;
  border-radius: 0.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.similar {
  height: 2rem;
  width: 90%;
  margin: 0.5rem 0.5rem 0 0.5rem;
  display: flex;
  align-items: center;
}
.cube3211 {
  background-color: #ffd600;
  border-radius: 0.4rem;
  justify-content: center;
  cursor: pointer;
}
.cube3212 {
  font-size: 0.9rem;
  font-weight: 400;
}
.cube3213 {
  height: 0;
  border-bottom: solid #e1e1e1;
}
.cube3214 {
  font-size: 1.4rem;
  font-weight: 550;
}
.cube3216 {
  width: 70%;
}
.cube3217 {
  color: #b52b00;
}
.para7 {
  color: #797979;
}
.para8 {
  color: #b52b00;
  font-size: 1.3rem;
  font-weight: 500;
}
.cube4 {
  width: calc(60% - 1rem);
  height: 3.5rem;
  border: solid #e1e1e1;
  border-radius: 0.4rem;
  margin: 0 1rem;
  display: flex;
}
.cube41 {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cube411 {
  background-color: #ffd600;
  height: 1.8rem;
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.4rem;
  cursor: pointer;
}
.cube42 {
  width: 70%;
}
.para5 {
  padding: 0.2rem;
  color: #b52b00;
  font-weight: 550;
  display: block;
}
.para6 {
  padding: 0.2rem;
  display: block;
  font-family: "Roboto";
  font-size: 0.9rem;
}
@media screen and (max-width: 599px) {
  .hero {
    margin-bottom: 3rem;
  }
  .cube1 {
    background-color: #fff;
    height: 2rem;
    width: 2rem;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.33);
    border-radius: 50%;
    margin: 0.5rem 1rem;
  }
  .behind {
    height: 2rem;
    width: 1rem;
    padding: 0.4rem 0;
    object-fit: contain;
  }
  .cube3 {
    flex-direction: column;
  }
  .cube31 {
    width: 100%;
  }
  .cube32 {
    width: 100%;
    display: none;
  }
  .cube4 {
    width: 90%;
    height: 4rem;
    margin: auto;
  }
  .cube411 {
    height: 2rem;
    width: 80%;
  }
}
