.hero {
  min-height: 3rem;
  width: 90vw;
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 0.5rem 5%;
}

.up {
  height: 3rem;
  width: 100%;
  display: flex;
}

.cube1 {
  width: 15%;
  height: 3rem;
  margin-left: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.cube2 {
  width: 60%;
  position: absolute;
  left: calc(15% + 1rem);
  top: 0.75rem;
  min-height: 3rem;
  /* max-width: 42rem; */
  overflow: scroll;

  display: flex;
}

.cube2::-webkit-scrollbar {
  display: none;
}

.cube3 {
  width: 10%;
  position: absolute;
  left: calc(75% + 1rem);
  top: 0.8rem;
  /* width: 8rem; */
  z-index: 1;
}
.down {
  min-height: 8rem;
  width: 100%;
  /*background-color: black;*/
}

.left {
  width: 80%;
  display: flex;
  flex-direction: row;
  /* background-color: grey; */
}

.btn {
  height: 1.5rem;
  object-fit: contain;
  cursor: pointer;
}

.right {
  width: 20%;
}

@media screen and (max-width: 599px) {
  .cube1 {
    display: none;
  }
  .cube2 {
    overflow: scroll;
    left: calc(11rem);
  }
  .cube2::-webkit-scrollbar {
    display: none;
  }
  .cube3 {
    left: 1rem;
  }
}
