* {
  box-sizing: border-box;
}
.btn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  border-radius: 0.4rem;
  cursor: pointer;
}
.img {
  height: 100%;

  object-fit: contain;
  padding: 0.5rem;
}
.para {
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
}
.cube1 {
  width: 8rem;
  height: 6vh;
  margin-left: 0.5rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  color: white;
  background-color: #2e0052;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.btn1 {
  width: 8rem;
  height: 60%;
  margin-left: 1rem;
  color: white;
  background-color: #2e0052;
}

.cube2 {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.img1 {
  height: 80%;
}
.para1 {
  font-size: 1.5rem;
  font-weight: 600;
}
.cube3 {
  width: 100%;
  min-height: 50vh;
  display: flex;
}
.cube30 {
  width: 68%;
  margin-left: 2%;
  /* margin: 1rem 2%; */
  height: 0;
  border: solid #e1e1e1;
}
.cube301 {
  width: 0;
  height: 83vh;
  position: relative;
  border: solid #e1e1e1;
}
.cube31 {
  width: 70%;
  padding: 0.5rem;
  margin: 0.5rem 0;
  max-height: 80vh;
  overflow: scroll;
}
.cube31::-webkit-scrollbar {
  display: none;
}
.cube32 {
  width: 30%;
  padding: 0.5rem;
}
.cube321,
.cube322 {
  height: 2.5rem;
  padding: 0.5rem;
  position: relative;
}
.para2,
.para3,
.para5 {
  position: absolute;
}
.para2,
.para5 {
  font-weight: 550;
}
.para4,
.para6 {
  position: absolute;
  left: 70%;
  /* margin-left: 50%; */
}
.cube322 {
  margin-top: 40%;
}
.cube323 {
  margin-top: 5%;
  height: 3rem;
}
.btn2 {
  height: 2rem;
  color: black;
  background-color: #ffd600;
}

.cube4 {
  height: 3rem;
  padding: 1rem;
  width: 70%;
  margin-bottom: 2rem;
}
.para7 {
  font-size: 1.2rem;
  margin-left: 25%;
  font-weight: 550;
}
.para8 {
  font-size: 1.2rem;
  margin-left: 50%;
  font-weight: 550;
}
@media screen and (max-width: 599px) {
  .hero {
    margin-bottom: 3rem;
  }
  .cube3 {
    flex-direction: column;
  }
  .cube30 {
    width: 95%;
  }
  .cube301 {
    display: none;
  }
  .cube31 {
    width: 100%;
  }
  .cube32 {
    width: 100%;
  }
  .cube322 {
    margin-top: 10%;
  }
  .btn2 {
    width: 60%;
    margin: auto;
  }
  .cube1 {
    background-color: #fff;
    height: 2rem;
    width: 2rem;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.33);
    border-radius: 50%;
    margin: 0.5rem 1rem;
  }
  .behind {
    height: 80%;
    padding: 0.4rem;
    object-fit: contain;
  }
}
