* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.hero {
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: center;
}
.header {
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0 1rem 0;
}
.headerText {
  margin-left: 0.5rem;
  font-size: 2rem;
  color: #2e0052;
}

.cube1 {
  width: 40%;
  min-height: 40vh;
  border: solid #d9d9d9;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.text {
  width: 80%;
  padding: 0.5rem 0rem;
  font-size: 1rem;
}
.capture {
  width: 80%;
  margin: 0px 1rem;
  height: 2rem;
}
.error {
  color: red;
  width: 80%;
  margin: 0px 1rem;
}
.text1 {
  font-size: 1.5rem;
  font-weight: 500;
}
.btn {
  width: 80%;
  height: 2rem;
  margin: 0.5rem 1rem;
  color: white;
  background-color: #2e0052;
  border-radius: 0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.cube2 {
  width: 40%;
  margin-top: 1rem;
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cube21 {
  width: 35%;
  height: 0;
  border: solid #d9d9d9;
}
.cube22 {
  padding: 0 1rem;
}
.btn2 {
  width: 40%;
  color: black;
  background-color: white;
  border: solid #bdbdbd;
  cursor: pointer;
}
.footer {
  margin-top: 6rem;
  height: 1.5rem;
  width: 100%;
  color: white;
  background-color: #2e0052;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cube3 {
  height: 1rem;
  width: 40%;
  display: flex;
  justify-content: center;
  margin: 1rem 0 0 0;
}
u {
  cursor: pointer;
}
@media screen and (max-width: 599px) {
  .hero {
    margin-bottom: 3rem;
  }
  .cube1 {
    width: 80%;
  }
}
